	height: 15px;
	margin-left: 5px;
	margin-top: 3px;
}

.take-photo svg {
	margin-top: 0;
}

.form_add_vehicle input[type="text"] {
  border: 1px solid rgba(0, 0, 0, 0.24);
  border-radius: 3px;
  padding: 5px;
}

.form_add_vehicle img {
	width: 25%;
}

.add_vehicle_container input[type=radio] {
	display: none;
}

.add_vehicle_container label {
	display: inline-block;
	cursor: pointer;
	padding: 0px 15px;
	line-height: 34px;
	border: 1px solid #999;
	border-right: none;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.add_vehicle_container .add_vehicle--item:first-child label {
  border-radius: 6px 0 0 6px;
}

.add_vehicle_container .add_vehicle--item:last-child label {
  border-radius: 0 6px 6px 0;
	border-right: 1px solid #999;
}

.add_vehicle--item {
  display: inline-block;
	float: left;   
}

.add_vehicle_container input[type=radio]:checked + label {
  background: rgb(206, 203, 203);
}

.vahicle--image--container {
  margin: 3px;
  width: 24%;
  position: relative;
}

.add__vahicle--image--container {
	margin: 3px;
  position: relative;
}

.add-vehicle--button {
  position: absolute;
  top: 5%;
  left: 21%;
  padding: 5px 8px;
